import * as React from 'react';

const BayerIFrame = () => {

	return (
		<div style={{}}>
			<iframe name="efPcsResume" id="efPcsResume" frameBorder="0" src="https://bayer.eightfold.ai/careers/resume/embed/?upload_redirect_url=https://career.bayer.ch&amp;config=%7B%22visibilityConfig%22%3A%7B%22showHeader%22%3Atrue%7D%2C%22textConfig%22%3A%7B%22headerText%22%3A%22%20%22%2C%22ctaText%22%3A%22Drop%20Resume%20Here%22%2C%22selectionText%22%3A%22Select%20File%22%7D%2C%22cssConfigUrl%22%3A%22https%253A%252F%252Fcareer.bayer.ch%252Fembed%252Fcss%252Fconfig.css%22%7D"
				title="resume"
				style={{
					zIndex: 100000000,
					width :"100%",
					height: "300px"
				}}
			>
			</iframe>
		</div>
	)

}

const bayerIFramePlugin = {
	match: 'bayer-iframe',
	component: BayerIFrame,
	options: {
		fullwidth: true
	}
}

if (!window.cognigyWebchatMessagePlugins) {
	window.cognigyWebchatMessagePlugins = []
}

window.cognigyWebchatMessagePlugins.push(bayerIFramePlugin);